.dashboard-container {
    padding: 80px;
}

.dashboard-container>h1 {
    color: var(--c3);
}

.main-card-dashboard {
    background-color: var(--c3);
    padding: 20px;
    border-radius: 12px;
    margin-bottom: 10px;
}

.event-details {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.event-detail-s1 {
    width: 50%;
    min-width: 300px;
}

.event-detail-s2 {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.event-detail-s2 button {
    width: 200px;
}

.event-details button {
    padding: 10px;
    border: 1px solid #cacaca;
    margin: 10px;
    cursor: pointer;
}

.dashboard-btn {
    border: 1px solid #cacaca;
    border-width: 1px;
}

.desk-btn {
    padding: 0px;
}

.desk-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}

.desk-team-info {
    display: flex;
    flex-direction: column;
    width: 30%;
    min-width: 300px;
}

.desk-team-info > div {
    display: flex;
    flex-direction: column;
    width: fit-content;
}

.participant-data {
    display: flex;
    flex-direction: column;
    width: 70%;
    min-width: 300px;
}

.team-details {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.member-data {
    width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.member-data img {
    width: 100px;
    height: 122px;
    border: 1px solid black;
    margin: 5px;
}

@media screen and (max-width: 665px) {
    .dashboard-container {
        padding: 10px;
    }

    .event-details {
        justify-content: center;
    }

    .event-detail-s1 {
        justify-content: center;
    }
}